.product__section {
    max-width: 1500px;
    min-height: 800px;
    margin: 3rem auto;
    display: grid;
    grid-template-columns: 70% auto;
    grid-gap: 0 2rem;
}

.product__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
}

.product__images img {
    width: 100%;
    height: 550px;
}

.product__info {
    min-width: 300px;
}

.product__info > * {
    margin: 0.5rem 0;
}

.product__info-top {
    display: flex;
    justify-content: space-between;
}

.product__name h1 {
    font-weight: 400;
}

.product__button {
    display: flex;
    justify-content: center;
    margin: 1rem 0;
}
