@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
.navbar__list {
	display: flex;
}

.navbar__item {
	margin-left: 3rem;
}

nav ul li a {
	color: #333;
	padding: 0 10px 10px 10px;
}

nav ul li a,
nav ul li a:after,
nav ul li a:before {
	transition: all 0.5s;
}

nav ul li a:hover {
	color: var(--color-grey);
}

nav.stroke ul li a {
	position: relative;
}

nav.stroke ul li a:after {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 0%;
	content: ".";
	color: transparent;
	background: var(--color-grey);
	height: 1px;
}

nav.stroke ul li a:hover:after {
	width: 100%;
}

.profile__menu {
	display: flex;
}

.profile__item {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-left: 1rem;
}

.profile__item i {
	cursor: pointer;
	color: #333;
	padding: 9px 7px;
	border-radius: 50%;
}

.profile__item i:hover {
	background: var(--color-hover-grey-light);
}

.cart-count {
	z-index: 10;
	position: absolute;
	top: -2px;
	display: flex;
	justify-content: center;
	align-items: center;
	color: white;
	background: red;
	font-size: 10px;
	line-height: 13px;
	font-weight: 600;
	width: 13px;
	height: 13px;
	border-radius: 50%;
}

.header {
	position: relative;
	background: #fff;
	width: 100%;
}
.header__bottom-window {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: auto;
	min-height: 60px;
}
hr {
	width: 100%;
	left: 0;
	position: absolute;
	border: 0.1px solid var(--color-white-light);
	z-index: 5;
}

.btn {
	cursor: pointer;
	border: none;
	background: transparent;
	font-weight: 700;
	color: #333;
	font-size: 1rem;
}

.btn a {
	color: #fff;
}

.btn--shop {
	text-decoration: underline;
}

.btn--order {
	margin: auto;
	font-size: 1rem;
	background: #333;
	color: #fff;
	padding: 0.7rem;
	border-radius: 10px;
	width: 100%;
}

.btn-shop a:hover {
	color: #444;
}

.btn--order:hover {
	color: #f4f4f4;
	background: #444;
}

.btn--remove {
	position: relative;
	color: var(--color-grey);
	font-weight: 400;
	transition: all 0.5s;
	padding-bottom: 2px;
	font-size: 0.9rem;
	text-decoration: underline;
}

.btn--remove:hover {
	color: var(--color-hover-grey);
}

.btn--black {
	border-radius: 15px;
	font-size: 1rem;
	padding: 0.6rem 1rem;
}
.btn--black {
	background: var(--color-black);
	color: #fff;
}

.btn--black:hover {
	background: #333;
	box-shadow: 0 0 4px var(--color-black);
}

.btn--link {
	width: 100%;
	display: block;
	text-align: center;
}

.btn--light {
	font-weight: 500;
	padding: 0.3rem 0.5rem;
	border-radius: 10px;
	background: var(--color-black);
	color: #fff;
}

.btn--light:hover {
	background: #555;
}

.select {
	font-size: 0.9rem;
	margin-left: 1rem;
}

.select--grey {
	color: var(--color-grey);
}

.select--border {
	border: 0px;
	background: none;
}

.product__item {
	padding: 1.5rem 0;
	border-bottom: 1px solid var(--color-white-light);
	display: grid;
	grid-template-columns: 200px 350px auto;
}

.product__item-name {
	font-size: 1.2rem;
}

.product__item-image {
	height: 150px;
	width: 150px;
	border-radius: 2px;
}

.product__item-info {
	position: relative;
	line-height: 1.6rem;
}

.product__item-info p {
	color: grey;
	font-size: 0.9rem;
}

.product__item-price {
	text-align: right;
	font-size: 0.9rem;
}

.product__item-info .btn--remove {
	position: absolute;
	bottom: 0;
}

.product__item-qty {
	color: var(--color-grey);
	font-size: 0.9rem;
}

.container--loading {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 90vh;
}

.lds-default {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
}
.lds-default div {
	position: absolute;
	width: 6px;
	height: 6px;
	background: var(--color-black);
	border-radius: 50%;
	-webkit-animation: lds-default 1.2s linear infinite;
	        animation: lds-default 1.2s linear infinite;
}
.lds-default div:nth-child(1) {
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
	top: 37px;
	left: 66px;
}
.lds-default div:nth-child(2) {
	-webkit-animation-delay: -0.1s;
	        animation-delay: -0.1s;
	top: 22px;
	left: 62px;
}
.lds-default div:nth-child(3) {
	-webkit-animation-delay: -0.2s;
	        animation-delay: -0.2s;
	top: 11px;
	left: 52px;
}
.lds-default div:nth-child(4) {
	-webkit-animation-delay: -0.3s;
	        animation-delay: -0.3s;
	top: 7px;
	left: 37px;
}
.lds-default div:nth-child(5) {
	-webkit-animation-delay: -0.4s;
	        animation-delay: -0.4s;
	top: 11px;
	left: 22px;
}
.lds-default div:nth-child(6) {
	-webkit-animation-delay: -0.5s;
	        animation-delay: -0.5s;
	top: 22px;
	left: 11px;
}
.lds-default div:nth-child(7) {
	-webkit-animation-delay: -0.6s;
	        animation-delay: -0.6s;
	top: 37px;
	left: 7px;
}
.lds-default div:nth-child(8) {
	-webkit-animation-delay: -0.7s;
	        animation-delay: -0.7s;
	top: 52px;
	left: 11px;
}
.lds-default div:nth-child(9) {
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
	top: 62px;
	left: 22px;
}
.lds-default div:nth-child(10) {
	-webkit-animation-delay: -0.9s;
	        animation-delay: -0.9s;
	top: 66px;
	left: 37px;
}
.lds-default div:nth-child(11) {
	-webkit-animation-delay: -1s;
	        animation-delay: -1s;
	top: 62px;
	left: 52px;
}
.lds-default div:nth-child(12) {
	-webkit-animation-delay: -1.1s;
	        animation-delay: -1.1s;
	top: 52px;
	left: 62px;
}
@-webkit-keyframes lds-default {
	0%,
	20%,
	80%,
	100% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.5);
	}
}
@keyframes lds-default {
	0%,
	20%,
	80%,
	100% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.5);
	}
}

.cart__section {
	max-width: 1000px;
	min-height: 93vh;
	margin: auto;
	padding: 2rem 0;
	display: grid;
	grid-template-columns: 65% auto;
	grid-gap: 3rem;
}

.cart__section-total {
	margin-top: 1rem;
	padding-top: 0.3rem;
	display: flex;
	border-top: 1px solid var(--color-grey-light);
}

.cart__section-total span {
	margin-left: 1rem;
	margin-bottom: 1rem;
	font-weight: 600;
}

.section__one {
	background: var(--color-white-light);
}
.section__one--img {
	width: 100%;
	height: 400px;
	overflow: hidden;
}
.section__one--img img {
	height: 100%;
}
.section__one--text {
	text-align: center;
	margin-right: 15rem;
}

.section__two {
	margin: 5rem auto;
	position: relative;
}

.section__two--grid {
	display: grid;
	grid-gap: 2rem;
	grid-template-columns: repeat(3, 1fr);
}

.grid__item {
	display: inline-block;
	padding: 0 1rem;
}

.grid__item img {
	height: 100%;
	width: 100%;
}

.section__two--text {
	padding-left: 5rem;
	font-size: 1.5rem;
}

.section__three {
	background: var(--color-white-light);
	margin: 5rem 0;
	padding: 0.5rem 0;
}
.section__three--grid {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 2rem;
	/* height: 570px; */
}
.section__three--text {
	text-align: center;
	margin-top: 1rem;
	width: 100%;
}
.section__three-item {
	position: relative;
	height: 600px;
	margin: auto;
	height: 80%;
	width: 100%;
	text-align: center;
}
.section__three img {
	/* height: 700px; */
	height: 100%;
	width: 100%;
}

@media (min-width: 1024px) {
	.section__three-item {
		height: 90%;
	}
}

.section__four {
	min-height: 300px;
	margin: 5rem 0;
}
.section__four-item {
	position: relative;
	width: 100%;
}
.section__four--grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 2rem;
}
.section__four--text {
	text-align: center;
	position: absolute;
	bottom: -3rem;
	width: 100%;
}
.section__four-item img {
	width: 100%;
	height: 100%;
}

.section {
	position: relative;
}

.section--flex {
	display: flex;
	align-items: center;
	align-content: center;
}

.section p {
	margin-bottom: 0.2rem;
}

.section__title {
	font-size: 1.4rem;
	margin-bottom: 0.2rem;
}

.bg-home {
	background: #f4f4f4;
}

.shop-menu {
	position: relative;
	margin: auto;
	min-height: 60px;
	border-bottom: 1px solid var(--color-white-light);
	margin-bottom: 1rem;
}
.shop-menu__items {
	width: 100%;
	display: flex;
	justify-content: flex-start;
	position: absolute;
	z-index: 10;
}
.menu-select {
	outline: none;
	height: 50px;
	font-size: 1rem;
	background: #fff;
	border: none;
	padding: 0.5rem;
}

.list-item {
	height: 450px;
	width: 100%;
	margin-bottom: 4rem;
}

.item-info {
	line-height: 1.3rem;
	display: flex;
	justify-content: space-between;
}

.info-color,
.info-status {
	font-size: 0.9rem;
	color: #777;
}

/* Images */

.image__container {
	margin: auto;
	height: 100%;
	width: 100%;
	margin-bottom: 1rem;
}

.image--front {
	height: 100%;
	width: 100%;
}

.shop-list {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 2rem;
}

@media (max-width: 1080px) {
	.shop-list {
		grid-template-columns: repeat(2, 1fr);
	}
}
@media (max-width: 585px) {
	.shop-list {
		grid-template-columns: repeat(1, 1fr);
	}
	.product__images {
		grid-template-columns: repeat(1, 1fr);
	}
}

.shop-section {
	margin: 2rem auto;
}

.contact--cart,
.contact--section {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.contact--section {
	min-height: 96vh;
	margin-top: 7rem;
}

.contact--section h1 {
	margin-bottom: 1rem;
}

.contact--cart {
	min-width: 450px;
}
.contact--cart .btn--black {
	width: 80%;
	margin: auto;
}

.footer {
	background: black;
	color: #fff;
	padding: 3rem 0 1.5rem 0;
}

.footer a {
	color: #fff;
}
.footer a:hover {
	color: var(--color-grey-light);
}

.footer hr {
	border: 0.5px solid #333;
}
.footer__grid {
	width: 90%;
	margin: auto;
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	grid-gap: 2rem;
	margin-bottom: 2rem;
}
.footer__item-contact {
	grid-column: 1 / span 2;
}
.footer__item {
	line-height: 2rem;
	font-weight: 300;
	font-size: 0.9rem;
	min-height: 200px;
}

.footer__title {
	font-size: 1rem;
	font-weight: bold;
}
.footer__list--social a {
	width: 30%;
	display: flex;
	align-items: center;
}
.footer__list--social i {
	padding-right: 0.5rem;
}

.footer__contact-email {
	font-weight: 700;
	text-decoration: underline;
}
.footer__bottom {
	display: flex;
	align-items: center;
	justify-content: center;
	color: #888;
	padding-top: 0.5rem;
	text-align: center;
	font-size: 0.8rem;
	font-weight: 300;
}

.pageNotFound {
    height: 96vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 600px;
    margin: auto;
    padding-bottom: 5rem;
}
.pageNotFound__title {
    line-height: 1.8rem;
    margin-bottom: 1rem;
}
.pageNotFound__title p {
    font-weight: 300;
    font-style: italic;
}

.product__section {
    max-width: 1500px;
    min-height: 800px;
    margin: 3rem auto;
    display: grid;
    grid-template-columns: 70% auto;
    grid-gap: 0 2rem;
}

.product__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
}

.product__images img {
    width: 100%;
    height: 550px;
}

.product__info {
    min-width: 300px;
}

.product__info > * {
    margin: 0.5rem 0;
}

.product__info-top {
    display: flex;
    justify-content: space-between;
}

.product__name h1 {
    font-weight: 400;
}

.product__button {
    display: flex;
    justify-content: center;
    margin: 1rem 0;
}

.dd-header {
	cursor: pointer;
	display: flex;
	align-items: center;
}

.dd-list {
	padding: 0.3rem 0.5rem;
	border-top: none;
	-webkit-animation: pop-up 0.25s ease-out;
	        animation: pop-up 0.25s ease-out;
}

.dd-list-item {
	padding-bottom: 0.3rem;
}

.dd--hover:hover {
	color: var(--color-grey);
	cursor: pointer;
}

.dd-header,
.dd-list-item {
	color: var(--color-dark);
}

.dd-wrapper span {
	transition: all ease-in 0.25s;
	margin-left: 0.5rem;
}

.dd-open span {
	transition: all ease-in 0.25s;
	transform: rotate(180deg);
}

.dd--border {
	border-bottom: 1px solid var(--color-white-light);
}

@-webkit-keyframes pop-up {
	0% {
		opacity: 0;
		transform: translateY(-20px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes pop-up {
	0% {
		opacity: 0;
		transform: translateY(-20px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

.form {
	position: relative;
}
.form--shipping {
	margin-top: 1rem;
	padding-top: 0.3rem;
}
.form-grid {
	display: grid;
	grid-gap: 0 1rem;
}

.grid--2 {
	grid-template-columns: repeat(2, 1fr);
}

.grid--3 {
	grid-template-columns: repeat(3, 1fr);
}

.grid--3 input {
	width: 100%;
}

.label {
	width: 100%;
	display: flex;
	flex-direction: column;
	color: var(--color-grey);
	margin-bottom: 1.5rem;
	font-size: 0.9rem;
}

.label input {
	color: var(--color-black);
	border: 1px solid var(--color-grey-light);
	min-height: 1.5rem;
	font-size: 1rem;
	font-family: sans-serif;
	margin-top: 0.3rem;
	padding: 0.3rem;
}

.error--message {
	font-size: 0.8rem;
	color: red;
	margin-top: 0.3rem;
}

.shipping__section {
	max-width: 1000px;
	min-height: 93vh;
	margin: auto;
	padding: 2rem 0;
	display: grid;
	grid-template-columns: 65% auto;
	grid-gap: 3rem;
}

.shipping__method {
	margin: 1rem 0 1rem 0;
	padding: 1rem 0 1.5rem 0;
	border-bottom: 1px solid var(--color-grey-light);
	border-top: 1px solid var(--color-grey-light);
}

.shipping__method h2 {
	margin-bottom: 1rem;
}

.shipping__method--form {
	position: relative;
	display: flex;
	width: 50%;
}

.form--shipping .select {
	background: none;
	border: var(--color-grey-light) 1px solid;
	padding: 0.3rem;
	margin: 0.3rem 0 0 0;
	width: 100%;
}
.form--shipping .btn {
	width: 200px;
}

.input--error {
	border: 1px solid red;
}

.order--label-radio {
	flex-direction: row;
	align-items: center;
}

.order--label-radio:not(:first-child) {
	margin-left: 1rem;
}

.order--label-radio input {
	margin: 0 0.6rem 0 0;
}

.order--label-radio .error--message {
	position: absolute;
	bottom: -20px;
	left: 0;
}

.order__section {
	max-width: 1000px;
	min-height: 93vh;
	margin: auto;
	padding: 2rem 0;
	display: grid;
	grid-gap: 3rem;
	grid-template-columns: 65% auto;
	grid-template-areas:
		"bag cart"
		"shipping cart";
}

.order__bag {
	grid-area: bag;
}

.order__shipping {
	grid-area: shipping;
	min-height: 500px;
}

.order__bag .cart__item {
	display: grid;
	grid-template-columns: 100px auto 70px;
	margin-top: 1rem;
}
.order__bag .cart__item .item-image--small {
	width: 80px;
	height: 80px;
}
.order__shipping--item {
	color: var(--color-grey);
	max-width: 450px;
	margin-top: 0.5rem;
	display: grid;
	grid-template-columns: 30% 1fr;
	border-bottom: 1px solid var(--color-grey-light);
	padding: 0.2rem;
}

.order__shipping--item p {
	font-weight: 600;
}

.login--section {
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.form--login {
    width: 300px;
}
.login--section .form-section {
    box-shadow: none;
}

.panel--item {
    margin-bottom: 1rem;
    display: flex;
}

.panel--item img {
    width: 75px;
    height: 75px;
}

.panel--item .product__item-info {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-left: 1rem;
}

.panel--item i {
    margin-right: 1rem;
}

.admin__section {
	position: relative;
	margin-top: 2rem;
	display: flex;
	justify-content: center;
}

.admin__section .logOut {
	position: absolute;
	top: 0;
	right: 0;
}
.admin__section-list {
	min-width: 600px;
}

.admin__section-list .title--border,
.admin__section-product .title--border {
	margin-bottom: 1rem;
}

.admin__section-top-panel {
	display: flex;
	justify-content: space-between;
}

.admin__section-top-panel span {
	margin-right: 1rem;
}

.admin__section-top-panel span:hover {
	color: var(--color-grey);
}

.admin__section a:hover i {
	color: var(--color-grey);
}

.admin__section-top-panel a:hover i {
	transform: rotate(90deg);
	transition: 0.5s;
}

.admin__section-product {
    min-width: 600px;
}

:root {
	--color-black: #333;
	--color-grey: #7d7d7d;
	--color-grey-light: #e4e4e4;
	--color-white-dark: #f6f6f6;
	--color-white-light: #f4f4f4;
	--width-desktop-large: 1800px;
	--media-desktop: 1200px;
	--media-tablet: 850px;
	--media-smartphone: 480px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    color: var(--color-black);
    font-family: "Roboto", sans-serif;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: var(--color-black);
}

.container {
    max-width: 1700px;
    margin: auto;
    padding: 0 2rem;
}

.success {
    color: green;
}

.danger {
    color: red;
}

h1,
h2,
h3 {
    font-weight: 400;
}

.form-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid var(--color-grey-light);
    box-shadow: 0 0 20px var(--color-grey-light);
    border-radius: 15px;
    padding: 1.5rem 3rem;
}

.form--label {
    width: 100%;
    display: flex;
    flex-direction: column;
    color: var(--color-grey);
    margin-bottom: 1.5rem;
    font-size: 0.9rem;
}
.form--label input,
textarea {
    color: var(--color-black);
    border: 1px solid var(--color-grey-light);
    min-height: 1.5rem;
    font-size: 1rem;
    font-family: sans-serif;
    margin-top: 0.3rem;
    padding: 0.3rem;
}
.form--label textarea {
    min-height: 12rem;
}

.mt-2 {
	margin-top: 1.5rem;
}

.mt-4 {
	margin-top: 4rem;
}

.title--border {
	border-bottom: 1px solid var(--color-grey-light);
}

.grid {
	display: grid;
	grid-gap: 0 1rem;
}



